import React from 'react';
import './style.scss';
import ApartamentAction from './ApartamentAction';
import ApartamentsComparison from './ApartamentsComparison';
import QueryActions from './QueryActions';
import HeaderBottom from '../Header/HeaderBottom/HeaderBottom';

const NotificationRequest = () => {
  console.log('NotificationRequest');
  return (
    <>
    <HeaderBottom />
    <div className="notificationRequest">
      <div className="NotificationRequest_btn_section">
        <button
          type="button"
          className="queryActions_btn"
        >
          Действия по запросам
        </button>
        <button
          type="button"
          className="apartmentActions_btn"
        >
          Действия по квартирам
        </button>
        <button
          type="button"
          className="apartmentsComparison_btn"
        >
          Квартиры на сравнение
        </button>
      </div>
      <div className="activeSection">
        <ApartamentAction />
        <ApartamentsComparison />
        <QueryActions />
      </div>
    </div>
    </>
  );
};

export default NotificationRequest;
