/* eslint-disable import/prefer-default-export */
import request from '../../constants/api';
import { FILTERED_OFFER_DATA, ACTIVE_FAVORIT } from '../actionType';

export const fileredOfferDataFetch = (fileredOfferData) => ({
  type: FILTERED_OFFER_DATA,
  fileredOfferData,
});

export const getFavoritList = () => (dispatch) => {
  request().get('/auth')
    .then((res) => request().post('/query/list?page=0&sortField=creationDate&sortDirection=DESC',
      { id: res.data.favorites })
      .then((res) => dispatch(fileredOfferDataFetch(res.data))))
    .catch(() => dispatch(fileredOfferDataFetch([])));
};

export const checkActiveFavorit = (id) => ({
  type: ACTIVE_FAVORIT,
  id,
});

export const getFavoritListId = () => (dispatch) => {
  request().get('./auth').then((res) => dispatch(checkActiveFavorit(res.data.favorites || [])));
};
