import React from 'react';
import './style.scss';
import AgentDatabase from './AgentDatabase';
import BlackList from './Blacklist';

const AgentBaseBlacklist = () => {
    console.log('AgentBaseBlacklist')

    return (
        <div className="agentBaseBlacklist">
            <div className="agentBaseBlacklist_btnGroup">
                <button
                    type="button"
                    className="agentBaseBlacklist_agentBtn"
                >
                    База агентов
                </button>
                <button
                    type="button"
                    className="agentBaseBlacklist_blackListBtn"
                >
                    Черный список
                </button>
            </div>
            <AgentDatabase />
        </div>
    );
};

export default AgentBaseBlacklist;
