import React, { useEffect } from 'react';
import './style.scss';
import { useDispatch } from 'react-redux';
import closeX from '../../assect/image/closeX.png';
import HeaderAddButtonHoc from '../../HOC/HeaderAddButtonHoc';
import { getListNotification } from '../../store/thunkAction/getListNotifications';

const Notification = () => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getListNotification());
  }, []);

  return (
    <>
      <div className="notification">
        <div className="notification_item">
          <div className="notification_title">Новое</div>
          <div className="notification_text">Гость Р просмотрел вашу запись 26.02.20.</div>
        </div>
        <div className="notification_closeBTN">
          <button type="button" className="button_img_hover">
            <img src={closeX} alt="closeX" />
          </button>

        </div>
      </div>
    </>
  );
};

export default HeaderAddButtonHoc(Notification);
