import React, { useState, useEffect } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Radio from '@material-ui/core/Radio';

const GreenRadio = withStyles({
  root: {
    color: '#6CC3C7;',
    width: '20px',
    height: '20px',
    '&$checked': {
      color: '#6CC3C7',
    },
  },
  checked: {},
})((props) => <Radio color="default" {...props} />);

const RadioBtnThreeGroup = ({
  classname, title1, title2, title3, onClick,
}) => {
  const [selectedValue, setSelectedValue] = useState();

  const handleChange = (event) => {
    setSelectedValue(event.target.value);
    onClick(event.target.value);
  };
  return (
    <div className={classname || ''}>
      <div>
        <GreenRadio
          checked={selectedValue === 'Не актуально'}
          onChange={handleChange}
          value="Не актуально"
          color="default"
          name="radio-button-demo"
          inputProps={{ 'aria-label': 'D' }}
        />
        <span>{title1}</span>
      </div>
      <div>
        <GreenRadio
          checked={selectedValue === 'Не интересует'}
          onChange={handleChange}
          value="Не интересует"
          name="radio-button-demo"
          inputProps={{ 'aria-label': 'm' }}
        />
        <span>{title2}</span>
      </div>
      <div>
        <GreenRadio
          checked={selectedValue === 'Сдал'}
          onChange={handleChange}
          value="Сдал"
          name="radio-button-demo"
          inputProps={{ 'aria-label': 'q' }}
        />
        <span>{title3}</span>
      </div>
    </div>
  );
};

export default RadioBtnThreeGroup;
