import React, { useState, useEffect } from 'react';
import './style.scss';

const InputElement = ({
  className, value, typeInp = 'text', w = '170px', backText = '', onChange, dis = false
}) => {
console.log(value)
  return (
    <div
      className={className || ''}
    >
      <input
        type={typeInp}
        className="rootInputContainer_inp"
        style={{ width: w }}
        placeholder={backText}
        onChange={(e)=>(e.target.value)}
        onChange= {onChange}
        value={value}
        disabled={dis}
      />
    </div>
  );
};

export default InputElement;
