import React, { useState, useEffect } from 'react';
import './style.scss';

import { useDispatch, useSelector } from 'react-redux';
import { nanoid } from 'nanoid';
import smallPersonIcon from '../../../assect/image/smallperson.png';
import starYUellowIcon from '../../../assect/image/star_icon.png';
import eyesIcons from '../../../assect/image/eyesIcons.png';
import hideIcon from '../../../assect/image/hideIcon.png';
import arrow_bottom from '../../../assect/image/arrow_bottom.png';
import penIcon from '../../../assect/image/penIcon.png';
import arrow_top from '../../../assect/image/arrow_top.png';
import addFavorit from '../../../assect/image/addFavorit.png';
import { getClientNumber } from '../../../store/thunkAction/getClientNumber';
import { getFileredOfferData } from '../../../store/selector/getFileredOfferData';
import { getClientNumberAuthor } from '../../../store/selector/getIdClientNumber';
import { filterSortByDateAdded } from '../../../store/thunkAction/filterApartaments';
import SortBy from '../../Header/HeaderBottomAllIcon/SortBy';
import HeaderAddButtonHoc from '../../../HOC/HeaderAddButtonHoc';
import { addFavoritList } from '../../../store/thunkAction/addFavoritList';
import { getFavoritListId } from '../../../store/thunkAction/getFavoritList';
import { unFolowFavorit } from '../../../store/thunkAction/unFolowFavorit';
import { getUserFio } from '../../../store/thunkAction/getUserFio';
import SimpleModal from '../../common/modal';
import NotificationModal from '../../common/notificationModal/notificationModal';
import { getUserFioId } from '../../../store/selector/getUserFioId';

const MyPosts = () => {
  const [tog, setTog] = useState([]);
  const [numTog, setNumTog] = useState(false);

  const filterApartaments = useSelector(getFileredOfferData);
  const clientNumberByPost = useSelector(getClientNumberAuthor);
  const userFioId = useSelector(getUserFioId);
  const dispatch = useDispatch();

  const fioGlobal = userFioId.fio;
  const handleClickSaveFavoritId = (id, active) => {
    if (!active) {
      dispatch(addFavoritList(id));
    } else {
      dispatch(unFolowFavorit(id));
    };
  };

  const handleTog = (id) => {
    const haveTog = tog.includes(id);
    if (!haveTog) {
      setTog((prevTog) => [...prevTog, id]);
    } else {
      setTog((prevTog) => prevTog.filter((currentId) => currentId !== id));
    }
  };

  useEffect(() => {
    const sortByDateAdded = {
      page: 0,
      sortfield: 'creationDate',
      sortDirection: 'DESC',
    };
    dispatch(filterSortByDateAdded(sortByDateAdded));
    dispatch(getFavoritListId());
    dispatch(getUserFio())
  }, []);
  const parseClientNumber = (num) => {
    const oneNum = num.slice(0, 3);
    const thoNum = num.slice(3, 6);
    const threeNum = num.slice(6, 8);
    const fourNum = num.slice(8, 10);
    const newNum = `+7(${oneNum}) ${thoNum}-${threeNum}-${fourNum}`;
    return newNum;
  };
  const newNumberByPost = parseClientNumber(`${clientNumberByPost}`);
  return (
    <>
      <SortBy />
      {
    filterApartaments ? filterApartaments.map((elem) => {
      const chidsAgeParse = elem.flat.childs.map((val) => {
        if (val.age === 0) {
          return '0 - 3 ';
        } if (val.age === 1) {
          return '4 - 5 ';
        } if (val.age === 2) {
          return '6 - 9 ';
        } if (val.age === 3) {
          return '10+ ';
        }
      });

      const districtParse = elem.district.map((val) => {
        if (val === 0) {
          return 'Ал-ка ';
        } if (val === 1) {
          return 'БВ ';
        } if (val === 2) {
          return 'ЧР ';
        } if (val === 3) {
          return 'ИЧ ';
        } if (val === 4) {
          return 'ЛБ ';
        }
      }, '');

      const renovationParse = (param) => {
        if (param === 0) {
          return 'Не важно';
        } if (param === 1) {
          return 'Косметический';
        } if (param === 2) {
          return 'Евро';
        }
      };

      const citizenshipParse = (param) => {
        if (param === 0) {
          return 'РФ';
        } if (param === 1) {
          return 'Другое';
        }
      };

      const clientStatusParse = (param) => {
        if (param === 0) {
          return 'Одиночка';
        } if (param === 1) {
          return 'Семья';
        } if (param === 2) {
          return 'Рабочий';
        } if (param === 3) {
          return 'ИТР';
        }
      };

      const petsParse = elem.flat.pets.map((param) => {
        if (param.type === 0) {
          return 'Другое ';
        } if (param.type === 1) {
          return 'Кошка ';
        } if (param.type === 2) {
          return 'Собака ';
        }
      }, '');

      const toISODate = (milliseconds) => {
        const date = new Date(milliseconds);
        const y = date.getFullYear();
        let m = date.getMonth() + 1;
        let d = date.getDate();
        m = (m < 10) ? `0${m}` : m;
        d = (d < 10) ? `0${d}` : d;
        return [y, m, d].join('.');
      };

      const toISODateArivalDate = (milliseconds) => {
        if (milliseconds) {
          const date = new Date(milliseconds);
          let m = date.getMonth() + 1;
          let d = date.getDate();
          m = (m < 10) ? `0${m}` : m;
          d = (d < 10) ? `0${d}` : d;
          return [+d, +m].join('.') || 'jaf';
        }
        return 'Отсуствует';
      };
      return (
        <div
          className="myPosts"
          key={elem.id}
          style={{
            border: elem.author.fio === fioGlobal ? '2px solid #BCDB9A' : '2px solid #6CC3C7',
          }}
        >
          <div
            className="myPosts_header"
            style={{
              backgroundColor: elem.author.fio === fioGlobal ? '#BCDB9A' : '#6CC3C7',
            }}
          >
            <div>
              {' '}
              {toISODate(elem.creationDate)}
              {' '}
            </div>
            <div>
              {elem.author.fio === fioGlobal ? '' : 'Автор-'}

              {elem.author.fio === fioGlobal ? 'Мои обьявлений' : elem.author.fio}
              <img src={smallPersonIcon} alt="person" className="myPosts_header_icons" />
            </div>
          </div>
          <div
            className="myPosts_body"
            style={{
              borderBottom: elem.author.fio === fioGlobal ? '2px solid #BCDB9A' : '2px solid #6CC3C7',
            }}
          >
            <div className="myPosts_body_icons"
            style={{ justifyContent: elem.author.fio === fioGlobal ? 'space-between' : 'space-evenly' }}
            >
              <button
                type="button"
                className="button_img_hover"
                onClick={() => handleClickSaveFavoritId(elem.id, elem.active)}
              >

                <img
                  src={elem.active ? addFavorit : starYUellowIcon}
                  alt="starYUellowIcon"
                  className="hover_gold"
                />
              </button>
              <div
                style={{ display: elem.author.fio === fioGlobal ? 'block' : 'none' }}
              >
                <NotificationModal
                  icon={eyesIcons}
                  id={elem.id}
                />
              </div>
              <SimpleModal icon={hideIcon} id={elem.id} />
            </div>
            <div className="myPosts_body_col">
              <div>
                <div className="myPosts_body_col_title">Район</div>
                <div>
                  {' '}
                  <div>{districtParse || 'нет'}</div>
                </div>
              </div>
              <div className="myPosts_body_col_bottom">
                <div className="myPosts_body_col_title">Кол-во комнат</div>
                <div>{elem.flat.rooms || 1}</div>
              </div>
            </div>
            <div className="myPosts_body_col">
              <div>
                <div className="myPosts_body_col_title">Гражданство</div>
                <div>{citizenshipParse(elem.citizenship)}</div>
              </div>
              <div className="myPosts_body_col_bottom">
                <div className="myPosts_body_col_title">Статус семьм</div>
                <div>{clientStatusParse(elem.client.clientStatus)}</div>
              </div>
            </div>
            <div className="myPosts_body_col">
              <div>
                <div className="myPosts_body_col_title">Дети</div>
                <div>
                  {
              elem.flat.childs.length ? elem.flat.childs.length : 'нет'
              }
                </div>
              </div>
              <div className="myPosts_body_col_bottom">
                <div className="myPosts_body_col_title">Животные</div>
                <div>{elem.flat.pets.length ? elem.flat.pets.length : 'нет'}</div>
              </div>
            </div>
            <div className="myPosts_body_col">
              <div>
                <div className="myPosts_body_col_title">
                  <span>Расчет</span>
                  {' '}
                  <span className="text_color">Наличные</span>
                </div>
                <div className="text_center">
                  {elem.price.min}
                  {' '}
                  -
                  {' '}
                  {elem.price.max}
                </div>
              </div>
              <div className="myPosts_body_col_bottom">
                <div className="myPosts_body_col_title">Дата заселения</div>
                <div className="text_center">{toISODateArivalDate(elem.arrivalDate)}</div>
              </div>
            </div>
          </div>
          <div className="myPosts_footer" style={{ borderBottom: tog ? 'none' : '2px solid #BCDB9A' }}>
            <button
              type="button"
              className="myPosts_footer_toggle_up"
              onClick={() => handleTog(elem.id)}
              style={{
                backgroundColor: elem.author.fio === fioGlobal ? '' : '#b9e4e6',
              }}
            >
              {' '}
              <img src={arrow_top} alt="arrow_top" />
              {' '}
            </button>
            <div />
          </div>
          <div className="myPosts_data" style={{ display: tog.includes(elem.id) ? 'flex' : 'none' }}>
            <div className="myPosts_body_icons">
              <button
                type="button"
                className="button_img_hover"
                style={{ display: elem.author.fio === fioGlobal ? 'block' : 'none' }}
              >
                <img src={penIcon} alt="penIcon" className="hover_gold" />
              </button>
            </div>
            <div className="myPosts_body_col">
              <div>
                <div className="myPosts_body_col_title">Имя</div>
                <div>{elem.fio}</div>
              </div>
              <div className="myPosts_body_col_bottom">
                <div className="myPosts_body_col_title">Телефон</div>
                <div />
                {elem.author.fio === fioGlobal ? parseClientNumber(elem.client.phone)
                  : (
                    <div className="number_btn_section">
                      <button
                        className="clientsForNumber_btn"
                        type="button"
                        onClick={
                          () => {
                            dispatch(getClientNumber(elem.id));
                            setNumTog(true);
                          }
                        }
                        style={{ display: numTog ? 'none' : 'block' }}
                      >
                        number
                      </button>
                      <div style={{ display: numTog ? 'block' : 'none' }}>
                        {newNumberByPost}
                      </div>
                    </div>
                  )}

              </div>
              <div className="myPosts_body_col_bottom">
                <div className="myPosts_body_col_title">Организация</div>
                <div>{elem.client.organization || 'нет'}</div>
              </div>
            </div>
            <div className="myPosts_body_col">
              <div>
                <div className="myPosts_body_col_title">Кол-во человек</div>
                <div>{elem.people}</div>
              </div>
              <div className="myPosts_body_col_bottom">
                <div className="myPosts_body_col_title">Дети</div>
                <ol type="1" style={{ paddingLeft: '20px' }}>{chidsAgeParse.map((elem) => <li key={nanoid()}>{elem}</li>)}</ol>
              </div>
            </div>
            <div className="myPosts_body_col">
              <div>
                <div className="myPosts_body_col_title">Ремонт</div>
                <div>
                  {renovationParse(elem.flat.renovation)}
                </div>
              </div>
              <div className="myPosts_body_col_bottom">
                <div className="myPosts_body_col_title">Животные</div>
                <ol type="1" style={{ paddingLeft: '20px' }}>
                  {petsParse.map((elem) => (
                    <li key={nanoid()}>{elem}</li>))}
                </ol>
              </div>
            </div>
            <div className="myPosts_body_col my_posts_body_coment">

              <div className="myPosts_body_col_title">Комментарий</div>
              <div
                className="myPosts_body_coment_container"
              >
                {elem.comment}
              </div>
            </div>
          </div>
          <div
            className="myPosts_footer"
            style={{ borderTop: tog ? 'none' : '2px solid #BCDB9A' }}
          >

            <button
              type="button"
              className="myPosts_footer_toggle_down"
              style={{
                backgroundColor: elem.author.fio === fioGlobal ? '' : '#b9e4e6',
              }}
              onClick={() => handleTog(elem.id)}
            >
              <img src={arrow_bottom} alt="arrow_bottom" />
            </button>
          </div>
        </div>
      );
    }) : 'nechego'
  }
    </>
  );
};

export default HeaderAddButtonHoc(MyPosts);
